//== Responsive visiblity classes
//

// Hidden on small
@media (max-width: $ms-screen-sm-max) {
  .ms-u-hiddenSm,
  .ms-u-hiddenMdDown,
  .ms-u-hiddenLgDown,
  .ms-u-hiddenXlDown,
  .ms-u-hiddenXxlDown {
    display: none !important;
